* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  --text-color: rgb(217, 252, 208);
}

body {
  width: 100vw;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgb(149, 237, 149);

  font-size: 18px;
  font-family: cursive;
  font-weight: bold;
}

#main-container {
  width: 40vw;
  height: 100vh;

  border-width: 0px 3px 0px 3px;
  border-color: black;
  border-style: solid;

  overflow-y: scroll;

  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

#main-container::-webkit-scrollbar {
  display: none; /*Chrome, Safari and Opera*/
}

#navbar-container {
  display: flex;
  align-items: center;
}

.navbar-item {
  flex: 1;
  text-align: center;
  padding: 10px 0px 4px 0px;
  cursor: pointer;
}

.info-text {
  text-indent: 1.5rem;
  padding: 20px 20px 0px 20px;
  hyphens: auto;
  line-height: 2rem;
  font-size: 20px;
}

.centered-icon {
  position: relative;
  top: 4px;
  padding-inline: 4px;
}

#search-bar-container {
  padding: 20px;
  width: 100%;

  display: flex;
  justify-content: center;
  gap: 10px;
}

#search-input {
  width: 80%;
  height: 36px;
  font-size: 20px;
  padding: 10px;
  border-radius: 18px;
  border: 2px solid black;
  background-color: rgb(8, 135, 8);
  color: var(--text-color);
  font-family: inherit;
}

#search-input::placeholder {
  font-family: inherit;
  color: var(--text-color);
}

#search-input:focus {
  outline: none;
  border: 2px solid black;
}

#search-button {
  width: 90px;
  height: 36px;
  border-radius: 18px;
  border: none;
  color: var(--text-color);
  background-color: green;
  border: 2px solid black;
  font-family: inherit;
  font-size: 20px;
  transition: all 0.4s ease;
  cursor: pointer;
}

#search-button:hover {
  transform: scale(1.02);
}

.tracklist-container {
  padding: 20px;
}

.track-container {
  border: 2px solid black;
  padding: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  color: var(--text-color);
  background-color: green;
  position: relative;
  font-weight: bold;
  margin-bottom: 20px;
  border-radius: 12px;
  transition: all 0.4s ease;
}

.track-container:hover {
  transform: scale(1.02);
}

.track-container:last-child {
  margin-bottom: 0px;
}

.audio-button {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid rgb(10, 72, 10);
  background-color: rgb(149, 237, 149);
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.4s ease;
}

.audio-button:hover {
  transform: scale(1.1);
}

.cover-image {
  width: 64px;
  height: 64px;
  object-fit: cover;
  border: 2px solid black;
}

.track-info {
  max-width: 60%;
}

.track-title {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.artist-name {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.track-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  border: 2px solid rgb(10, 72, 10);
  border-radius: 50%;
  background-color: rgb(149, 237, 149);
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.4s ease;
  display: flex;
  justify-content: center;
  align-items: center;
}

.track-button:hover {
  transform: translateY(-50%) scale(1.1);
}

.add-button {
  right: 45px;
}

.discover-button {
  right: 10px;
}

#discover-track-container {
  padding: 20px;
}

#discover-track-container .track-container {
  margin-bottom: 0px;
}

#discover-text {
  padding: 10px;
  padding-bottom: 0px;
  margin-bottom: 0px;
}

#playlist-header {
  padding-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#export-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  margin-right: 40px;
}

@media screen and (max-width: 1080px) {
  #main-container {
      width: 100vw;
  }
}